<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div id="divMainLeadData" v-cloak>

            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">Manage Campaign</span>
                        <span class="float-right">
                            <!-- <a href="javascript:;" class="p-action-btn text-white" title="PageTour"
                                @click="setTourGuideSteps(0)">
                                <em class="fa fa-road" aria-hidden="true"></em>
                                <br />PageTour
                            </a> -->
                        </span>
                    </div>
                </div>
            </div>


            <div class="border p-3">
                <Form v-slot="{ valid,errors }" ref="form">
                    <DynamicForm :schema="GroupBy" :dateformat="dateFormat" v-on:setDependentDate="setDependentDate"
                        :DependentOption="DependentOption" v-on:onSearchDDL="onSearch" v-on:setSelectedValue="setSelectedValue"
                        v-on:onBlur="onBlur" />
                    <div class="row flex-row-reverse mt-2">
                        <div class="col-lg-6 text-right">
                            <a href="javascript:;" v-bind:class="{ disabled: false }" v-on:click="SubmitData(valid,errors)"
                                class="btn btn-success btn_save_a" title="Save">
                                <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                            </a>&nbsp;
                            <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                <em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}
                            </a>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <small class="text-danger">{{ $t('MandatoryString') }}</small>
                            </div>
                        </div>
                    </div>
                </Form>
            </div>
        </div>
      
    </section>
</template>

<script>
import CampaignDataServices from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";
import $ from "jquery";
export default {
    components: {
        DynamicForm
    },
    data() {
        return {
            isLoading: false,
            CampaignId: this.$route.params.id,
            CampaignData: null,
            LeadOwner: null,
            GroupBy: [],
            Channel: null,
            IndustryType: null,
            Status: null,
            StartDate: '01/01/1990',
            DependentOption: [{
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            },
            {
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            }
            ],
            DependentOptionEdit: [{
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date('01/01/1990')
            },
            {
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date('01/01/1990')
            }
            ],
            selected: false,
            selectoptions: [],
            vSelectValidate: false,
            submited: false,
            dateFormat:null
        }
    },
    created: function () {
        var VueObj = this;
        this.dateFormat=this.GetUserInfo.DateFormat;
        VueObj.FetchData();
    },
    methods: {
        onCancel: function () {
            this.$router.push({ name: 'CRMCampaign' });
        },
        setDependentDate(item) {
            var checkDate = item.value;
            if (item.name == 'campaign_start_date' || item.name == 'campaign_actual_start_date') {
                this.DependentOption[0] = {
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: item.value
                }
                var fieldName = item.name == 'campaign_actual_start_date' ? 'campaign_actual_end_date' : 'campaign_end_date';
                checkDate = this.dateComapre(this.$options.filters.formatDate(item.value), $(`div[name='${fieldName}']`).attr('value'));

            }
            var thisObj = this;
            setTimeout(function () {
                if (item.name == 'campaign_start_date') {
                    $.each(thisObj.GroupBy, function (filedIndex, fieldItem) {
                        $.each(fieldItem.Data, function (index, element) {
                            if (element.name == "campaign_end_date") {
                                element.value = checkDate;
                            }
                        });
                    });
                } else if (item.name == 'campaign_actual_start_date') {
                    $.each(thisObj.GroupBy, function (filedIndex, fieldItem) {
                        $.each(fieldItem.Data, function (index, element) {
                            if (element.name == "campaign_actual_end_date") {
                                element.value = checkDate;
                            }
                        });
                    });
                }
            }, 1000)
        },
        dateComapre(date1, date2) {
            var result = null;
            if (date2 == undefined)
                date2 = date1;
            else
                date2 = this.$options.filters.formatDate(new Date(date2));
            let startDate = Date.parse(date1);
            let endDate = Date.parse(date2);
            if (startDate > endDate) {
                this.DependentOption[1]['minDate'] = date1;
                result = date1;
            } else {
                this.DependentOption[1]['minDate'] = date2;
                result = date2;
            }
            return result;
        },
        SubmitData: function (values) {
            var VueObj = this;
            // VueObj.CampaignData.forEach((item) => {
            //     if (item.astype != '' && item.form_field_visibility == 'YES') {
            //         if (item.dt_code == "select" && item.value) {
            //             try {
            //                 values[item.name] = String((typeof item.value === 'object' && item.value !== null) ? item.value.value : item.value);
            //                 VueObj.$refs.form.setFieldTouched(item.name, false);
            //             } catch (ex) { console.log('error while get dropdown parse data') }
            //         }
            //         if (item.dt_code == 'date' && item.value) {
            //             values[item.name] = VueObj.$options.filters.formatDate(item.value);
            //             VueObj.$refs.form.setFieldTouched(item.name, false);
            //         } else {
            //             VueObj.$refs.form.setFieldTouched(item.name, true);
            //         }
            //     }
            // })
            VueObj.$refs.form.validate().then((result) => {
                VueObj.vSelectValidate = true;
                if (result) {
                    VueObj.submited = true;
                    VueObj.isLoading = true;
                    var fullJson = VueObj.CampaignData;
                    var data = [];
                    var validation = [];
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        var fieldValue = (item.name == "campaign_id" ? VueObj.CampaignId : item.value);
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                            } catch (ex) { }
                        }
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                    });
                    var postJSON = {
                        data: data,
                        validation: validation
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    CampaignDataServices.PostCampaign(objectPostString).then(function (response) {
                        VueObj.isLoading = false;
                        if (response.data[0].Code == "1") {
                            if (VueObj.CampaignId > 0) {
                                VueObj.ShowAlert(VueObj.$t('UpdatedSuccess', [VueObj.$t('Campaign')]), "success", true, VueObj.$t("Alert"));
                            }
                            else {
                                VueObj.ShowAlert(VueObj.$t('AddedSuccess', [VueObj.$t('Campaign')]), "success", true, VueObj.$t("Alert"));
                            }
                            VueObj.$router.push({ name: 'CRMCampaign' });
                        }
                        else {
                            VueObj.ShowAlert(VueObj.$t('AddedError', [VueObj.$t('Campaign')]), "failure", true, VueObj.$t("Alert"));
                        }
                        VueObj.submited = false;
                    });

                }
            });
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading = true;
            const params = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}&langCode=en`;
            CampaignDataServices.getFormLayout(params).then(function (response) {
                vm.isLoading = false;
                vm.CampaignData = response.data.data;
                if (vm.CampaignData != null && vm.CampaignData.length > 0) {
                    var groupId = vm.CampaignData[0].group_id;
                    $.each(vm.CampaignData, $.proxy(function (index, item) {
                        groupId = item.group_id;
                        if (item.dt_code == 'date') {
                            item.value = item.value == '' ? null : item.value;
                        }
                        if (item.dt_code == 'checkbox') {
                            item.select_value = item.value.split(',');
                        }
                        var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vm.CampaignData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (vm.CampaignId > 0) {
                                $.each(returnedData, function (filedIndex, fieldItem) {
                                    if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                                        fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                        fieldItem.select_value = fieldItem.select_options;
                                        if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                                            fieldItem.value = fieldItem.select_options[0];
                                        }
                                    }
                                    else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES" && fieldItem.value > 0) {
                                        fieldItem.value = parseFloat(fieldItem.value, 10).toFixed(2);
                                    }
                                });
                            }
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    group_display_name: returnedData[0].group_display_name,
                                    Data: returnedData
                                };
                                vm.GroupBy.push(objGroup);
                            }
                        }
                    }))
                    if (vm.CampaignId > 0) {
                        var frmFields = {}
                        vm.CampaignData.forEach((item) => {
                            if (item.astype != '' && item.form_field_visibility == 'YES') {
                                if (item.name == 'campaign_owner' && typeof item.value === 'object' && item.value !== null) {
                                    frmFields[item.name] = item.value.value;
                                } else {
                                    frmFields[item.name] = item.value;
                                }
                            }
                        })
                        vm.$refs.form.resetForm({ values: frmFields });
                    }
                    setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
                    setTimeout(function () { vm.RadioButtonBootstrap() }, 500);
                    // setTimeout(function () { vm.ShowPopOver() }, 500);
                }
            });
        },
        onSearch: function onSearch(search, objItem) {
            if (search) {
                this.searchSelectData(search, this, objItem);
            }
        },
        searchSelectData: function (search, vm, objItem) {
            this.ShowSearchData(search, vm, objItem, objItem.value.value);
        },
        ShowSearchData(search, vm, objItem, selctedValue) {
            if (typeof (selctedValue) == 'undefined') {
                selctedValue = 0;
            }
            var url = "id=&moduleName=" + this.$route.params.moduleName + "&type=" + objItem.field_code;
            if (objItem.dependent_type == 'fill') {
                url += "&refId=" + selctedValue;
            }
            CampaignDataServices.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = [];
                    if (response.data == '') {
                    }
                    if (response.data != '') {
                        json = response.data;
                    }
                    objItem.select_options = json.DATA;
                    return objItem.select_options;
                });
        },
        setSelectedValue(item, val) {
            var vm = this;
            // vm.$refs.form.setFieldTouched(item.name, false);
            try {
                if (Array.isArray(val)) {
                    item.value = (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                }
                else {
                    if (item.has_dependent >= 0) {
                        vm.GroupBy.forEach((group) => {
                            group.Data.forEach((childItem) => {
                                if (childItem.parent_id == item.custom_field_id && childItem.dependent_type == 'fill') {
                                    vm.ShowSearchData('', vm, childItem, val.value);
                                }
                            })
                        })
                    }
                    item.value = val;
                }
            }
            catch (ex) {
            }
        },
        onBlur(item) {
            try {
                if (String(item.value).length > 1) {
                    item.value = Number.parseFloat(item.value).toFixed(2);
                    this.$refs.form.setFieldValue(item.name, item.value);
                }
            } catch (ex) {

            }
        }
    }
}
</script>